.product__details {
   padding-left: 76px;
   padding-right: 76px;
}

.go__back {
    box-sizing: border-box;
    display: flex;
    padding-top: 48px;
    font-size: 18px;
    cursor: pointer;
    font-family: "Inter", sans-serif;
    font-weight: 600;
    padding-left: 8px;
    padding-bottom: 32px;
}

.go__back::before {
    content: url(../../icons/arrowback.png);

}

.slider__img {
    width: 848px;
    height: 566px;
    background-color: grey;
    border-radius: 16px;
    overflow: hidden;
}

.slider__img img {
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
}

.other__img1, .other__img2 {
    width: 408px;
    height: 267px;
    overflow: hidden;
    border-radius: 8px;
    background-color: grey;
}

.other__img1 img, .other__img2 img {
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
}

.details__wrapper {
    display: flex;
    justify-content: space-between;
    padding-bottom: 80px;
}

.other__img {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.favotite__title {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px 0;
    width: 192px;
    font-size: 18px;
}

.favotite__title svg path {
    fill: black;
}

.favotite__title svg {
    margin-left: 8px;
}

.favorive {
    display: block;
    cursor: pointer;
    background: none;
    border: 2px solid transparent;
    border-radius: 4px;
    transition: border 0.3s;
}

.favorive:hover {
    border-color:#00976F;
}

.map__btn {
    display: block;
    box-sizing: border-box;
    background: none;
    border: 2px solid transparent;
    border-radius: 4px;
    cursor: pointer;

    transition: border 0.3s;
}

.map__btn:hover {
    border-color:#00976F;
}


.map__btn svg path {
    fill: black;
}

.favotite__title1 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px 0;
    width: 192px;
    font-size: 18px;
}

.favotite__title1 svg {
    margin-right: 8px;
}

.price__header {
    display: flex;
    justify-content: space-between;
    padding-bottom: 41px;
}

.price__header__wrapper {
    display: flex;
}

.price__header__wrapper >:not(:first-child) {
    margin-left: 32px;
}

.id__wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 494px;
    width: 100%;
    padding-bottom: 16px;
}

.localization {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Inter", sans-serif;
    font-size: 16px;
    font-weight: 500;
    color: #1B1B1B;
}

.localization::before {
    content: url(../../icons/location-on-outline.svg);
    padding-right: 15px;
}

.id {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #8E8E8E;
    font-family: "Inter", sans-serif;
    font-size: 16px;
    font-weight: 500;
}

.id span {
    color: #1B1B1B;
    padding-left: 16px;
}

.description {
    display: flex;
    justify-content: space-between;
}

.description__wrapper {
    max-width: 848px;
    width: 100%;
}

.description__title {
    font-size: 30px;
    font-weight: 600;
    line-height: 40px;
    font-family: "Inter", sans-serif;
    padding-bottom: 40px;
}

.description__goods {
    display: flex;
    padding-top: 42px;
    padding-bottom: 22px;
    padding-left: 16px;
    padding-right: 50px;
    border: 1px solid #E2E2E2;
    box-shadow: 1px 4px 12px 0px rgba(100, 114, 125, 0.08);
    border-radius: 12px;
    margin-bottom: 80px;
    word-wrap: break-word;
}

.description__goods > :not(:first-child) {
    padding-left: 60px;
}

.goods__wrapper {
    display: flex;
    flex-direction: column;
}

.good {
    display: flex;
    padding-bottom: 20px;
    width: 100%;
}

.desc {
    padding-left: 50px;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: 0.018px;
}

.type {
    width: 135px;
    display: flex;
}

.type::before {
    content: url(../../icons/location-on-outline.svg);
    padding-right: 8px;
    margin-top: -2px;
}

.room::before {
    content: url(../../icons/bed.png);
}

.area::before{
    content: url(../../icons/floor-plan.png);
}

.floor::before{
    content: url(../../icons/stairs.png);
}

.state::before{
    content: url(../../icons/paint-brush-line.png);
}

.heating::before{
    content: url(../../icons/white-balance-sunny.png);
}

.waterheating::before{
    content: url(../../icons/heating-coil.png);
}

.buildingtype::before{
    content: url(../../icons/outline-house.png);
}


.full__desc__wrapper {
    max-width: 848px;
    width: 100%;
    padding-top: 24px;
    padding-bottom: 80px;
}

.full__desc {
    font-size: 18px;
    color: #8E8E8E;
    line-height: 28px;
    font-family: "Inter", sans-serif;
    font-weight: 500;
}

.full__title {
    font-size: 24px;
    font-family: "Inter", sans-serif;
    font-weight: 700;
}

.map__wrapper {
    padding-bottom: 100px;
}

.card__container {
    position: relative;
    overflow: scroll;
}



